body {
    overflow-y: scroll;
}

h1, h2 {
    font-family: 'Oswald', sans-serif;
}

.row > h1 {
    font-size: 75px;
    padding: 50px 0px 25px 100px;
}

h2 {
    padding: 10px 0 0 50px;
}
.cards {
    padding: 30px 0 25px 50px;
}
.video-item {
    display: flex !important;
    align-items: center !important;
    cursor: pointer;
}

.video-item.item img {
    max-width: 180px;
}
.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 40px;
}

.image-list img {
    width: 250px;
}
.season-display {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.winter {
    background-color: aliceblue;
}

.season-display.winter i {
    color: blue;
}

.summer {
    background-color: orange;
}

.season-display.summer i {
    color: red;
}

.icon-left {
    position: absolute;
    top: 10px;
    left: 10px;
}

.icon-right {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

.center {
    margin: auto 0;
}
